import Loading from '../../public/Loading'
import Bus from '../../assets/eventBus'
import None from '../../public/noNumber'
import Person from '../../public/person.vue'
  export default {
      name: "SystemMessage",
    data(){
        return{
          api: this.GLOBAL.baseURL,
          messageList:[],
          total:0,
          isLoading:false,
          userId:'',
          index:1,
          page:1,
          clearNum:''
        }
    },
    components:{
        Person,
      None,
      Loading
    },
    methods:{
        readall(){
          this.$http.post(this.api + '/SysMsgReadAll',{
            token: localStorage.getItem('token')
          })
            .then(res=>{
              if(res.data.code == 1){
                this.clearNum = ''
                this.getMessage()
                Bus.$emit('clearAll',1)
              }else if(res.data.code == 3){
                this.readall()
              }else{
                alert(res.data.msg)
              }
            })
        },
      handleCurrentChange(val){
        this.page = val
        this.getMessage()
      },
        check(item,index){
          if(!item.read_user_list.length){
            this.sendNumber()
          }
            this.$router.push({
            path:'/messagePage',
            query:{
              information:item.msg_id
            }
          })
        },
      getMessage(){
        this.isLoading = true
          this.$http.post(this.api + '/sysmsgl',{
            token: localStorage.getItem('token'),
            page:  this.page
          })
            .then(res=>{
              if(res.data.code == 1){
                this.total = res.data.data.total
                this.messageList = res.data.data.data
                this.isLoading = false
              }else if(res.data.code == 3){
                this.$http.post(this.api + '/home/index/token')
                  .then(res=>{
                    localStorage.setItem('token',res.data.data)
                    this.getMessage(val)
                  })
              }else if(res.data.code == 0){
                this.isLoading = false
                alert(res.data.msg)
              }
            })
      },
      sendNumber(){
        Bus.$emit('reduce',1)
      }
    },
    mounted() {
        this.getMessage(1)
    },
    created(){
        this.userId = this.$route.query.information
      this.clearNum = this.$route.query.clearNum
      Bus.$on('alearAll',msg =>{
        if(msg){
          this.clearNum = 0
        }
      })
    }
  }



